._leftPanel {
  /* border: 3px solid white; */
  list-style: none;
  padding: 1em;
  display: grid;
  grid-template-areas: 
    "title1 title1 title1"
    "content content content"
    "title2 title2 image"
    "cards cards image";
}
._leftPanel > h3 {
  grid-area: title1;
}
._leftPanel > p {
  grid-area: content;
}
._leftPanel > h4 {
  grid-area: title2;
}
._leftPanel > img {
  grid-area: image;
}
._leftPanel > ul {
  grid-area: cards;
}
.logo {
  animation-name: spin;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.dropdown:hover > *{
 display: block;
}